@keyframes slider {
  0% {
    transform: translateX(0);
  }
  15% {
    transform: translateX(-1rem);
  }
  35% {
    transform: translateX(1rem);
  }
  50% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(0);
