}
.before-after {
  position: relative;
  height: 59.3rem;
}
.before-after img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: unset;
  user-select: none;
}
.before-after__before {
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 50%;
  height: 100%;
  z-index: 2;
}
.before-after__after {
  height: 100%;
}
.before-after__change {
  position: absolute;
  z-index: 5;
  left: 50%;
  top: 0;
  width: 5px;
  background-color: #fff;
  height: 100%;
  cursor: pointer;
}
.before-after__change::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 12px;
  height: 12.6rem;
  border-radius: 5px;
}
.before-after__change-prev {
  position: absolute;
  top: 50%;
  left: -2.7rem;
  transform: translateX(-100%) translateY(-50%);
  width: 1.4rem;
  height: 2.7rem;
  background-image: url("./../img/arr-prev.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.before-after__change-next {
  position: absolute;
  top: 50%;
  right: -2.7rem;
  transform: rotateY(180deg) translateX(-100%) translateY(-50%);
  width: 1.4rem;
  height: 2.7rem;
  background-image: url("./../img/arr-prev.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.optima {
  padding-top: 0.7rem;
  padding-bottom: 7rem;
  background-color: #1d1b1a;
}
.optima__title {
  margin-bottom: 4.8rem;
  max-width: 82rem;
}
.optima__title span {
  transform: rotate(-1.6deg) translateY(0.2rem);
}
.optima__container {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 6rem;
}
.optima__left {
  width: 100%;
  max-width: 39rem;
  flex-shrink: 0;
}
.optima__desc p {
  margin: 0;
}
.optima__desc p:not(:last-child) {
  margin-bottom: 1.6rem;
}
.optima__right {
  flex-grow: 1;
  position: relative;
}
.optima__levels {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
}
.optima__lvl {
  font-weight: 400;
  font-size: 1rem;
  line-height: 110%;
  letter-spacing: -0.01em;
  color: rgba(245, 245, 245, 0.9);
  text-align: center;
}
.optima__lvl--premium {
  padding-top: 4rem;
  padding-bottom: 3rem;
  border-top: 1px dashed rgba(181, 109, 37, 0.5);
}
.optima__lvl--optimal {
  padding-top: 3rem;
  padding-bottom: 3rem;
  border-top: 1px dashed rgba(181, 109, 37, 0.5);
}
.optima__lvl--wrong {
  padding-top: 2rem;
  color: rgba(255, 0, 0, 0.9);
  border-top: 1px dashed rgba(255, 0, 0, 0.5);
}
.optima__slides {
  display: flex;
}
.optima__slide {
  padding-top: 28rem;
  flex: 1;
  font-weight: 400;
  font-size: 1rem;
  line-height: 110%;
  letter-spacing: -0.01em;
  text-align: center;
  color: rgba(245, 245, 245, 0.8);
  position: relative;
  transition-duration: inherit;
}
.optima__slide-line {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 27rem;
  width: 0.4rem;
  background-color: rgba(245, 245, 245, 0.3);
}
.optima__slide-line::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-40%);
  width: 1.6rem;
  height: 2.5rem;
  box-shadow: 0 2px 6px 5px rgba(0, 0, 0, 0.51);
  background: linear-gradient(180deg, #fff 0%, #b1b1b1 17%, #fff 66%, #e5e5e5 79.5%, #aaa 100%);
  border: 0.5px solid #c0c0c0;
  border-radius: 2px;
  transition: transform;
  transition-duration: 3s;
}
.optima__items {
  display: flex;
  border-top: 1px solid rgba(245, 245, 245, 0.2);
}
.optima__item {
  flex: 1;
  padding-right: 2rem;
}
.optima__text {
  max-width: 16rem;
}
.optima__text span {
  display: block;
}
.optima__slider {
  padding-top: 1.7rem;
}
.optima__capture {
  margin-bottom: 1.5rem;
}
.optima__icon {
  width: 2.8rem;
  aspect-ratio: 1/1;
  border: 1px solid rgba(245, 245, 245, 0.2);
  border-radius: 100%;
  background-color: #161616;
  padding: 0.6rem;
  transform: translateY(-50%);
}
.optima__icon svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: rgba(245, 245, 245, 0.3);
}

.possible {
  padding: 8rem 0;
}
.possible__title {
  max-width: 80rem;
  margin-bottom: 8rem;
}
.possible__title span {
  line-height: 0;
  transform: rotate(-2deg) translate(0rem, 1.6rem);
}
.possible__items {
  display: flex;
  gap: 2.5rem;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  margin-bottom: 4rem;
}
.possible__item {
  flex: 1;
}
.possible__icon {
  transform: translateY(-50%);
  width: 5.5rem;
  aspect-ratio: 1/1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  border: 1px solid rgba(29, 27, 26, 0.2);
  background-color: #f5f5f5;
  border-radius: 100%;
  position: relative;
}
.possible__icon::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: -0.5rem;
  width: 2rem;
  height: 1.5rem;
  background-image: url("./../img/check.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.possible__icon img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.possible__desc b {
  display: block;
}

.browser {
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 7px 37px 0 rgba(0, 0, 0, 0.2), 0 15px 22px 0 rgba(0, 0, 0, 0.3), 0 0 1px 0 rgba(0, 0, 0, 0.7);
  background: #fff;
}
.browser__header {
  background: linear-gradient(180deg, #dfe1e5 0.6%, #dfe1e5 99.4%);
  padding: 0.8rem 1.3rem 0;
  display: flex;
  align-items: flex-end;
  gap: 0.5rem;
}
.browser__control {
  display: flex;
  gap: 0.8rem;
  margin-bottom: 1.4rem;
}
.browser__dot {
  width: 1.2rem;
  aspect-ratio: 1/1;
  border-radius: 100%;
  flex-shrink: 0;
}
.browser__dot--close {
  background-color: #ed6a5e;
  box-shadow: inset 0 0 6px 0 #ec6d62;
}
.browser__dot--min {
  background-color: #f6be4f;
  box-shadow: inset 0 0 6px 0 #f5c451;
}
.browser__dot--fullscreen {
  background-color: #62c554;
  box-shadow: inset 0 0 6px 0 #68cc58;
}
.browser__tab {
  position: relative;
}
.browser__tab::after {
  content: "";
  position: absolute;
  left: calc(100% + 0.8rem);
  top: 50%;
  transform: translateY(-50%);
  width: 1.2rem;
  height: 1.2rem;
  background-image: url("./../img/cross-new.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.browser__tab-content {
  margin-bottom: -0.1rem;
  display: flex;
  align-items: center;
  gap: 0.7rem;
  width: 25.6rem;
  mask-image: url("./../img/tab.svg");
  mask-size: 100% auto;
  mask-position: bottom;
  mask-repeat: no-repeat;
  border-radius: 0.5rem 0.5rem 0 0;
  overflow: hidden;
  background-color: #fff;
  padding: 0.9rem 2rem;
  position: relative;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 1.2rem;
  white-space: nowrap;
  overflow: hidden;
  color: #3d4043;
}
.browser__tab-content::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  height: 100%;
  width: 30%;
  background: linear-gradient(270deg, #fff 40%, transparent 100%);
}
.browser__tab-content img {
  display: block;
  width: 1.8rem;
  aspect-ratio: 1/1;
  flex-shrink: 0;
  object-fit: contain;
  border-radius: 100%;
  overflow: hidden;
}
.browser__tab-content span {
  position: absolute;
  z-index: 2;
  top: 50%;
  right: 2rem;
  width: 0.8rem;
  aspect-ratio: 1/1;
  transform: translateY(-50%);
}
.browser__tab-content span svg {
  display: block;
  width: 100%;
  height: 100%;
}
.browser__content {
  height: 52.7rem;
  overflow: hidden;
  position: relative;
}

.browser-content__items {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.8rem;
}
.browser-content__title {
  margin: 0;
  margin-bottom: 1.5rem;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 2.3rem;
  color: #1d1b1a;
}
.browser-content__body {
  padding: 2.4rem 3.4rem;
}

.browser-item {
  position: relative;
  border: 1.08px solid rgba(73, 73, 73, 0.2);
}
.browser-item__label {
  position: absolute;
  top: 0.8rem;
  left: 0.8rem;
  padding: 0.4rem 1rem;
  border: 0.81px solid rgba(22, 22, 22, 0.2);
  background: #f5f5f5;
  font-size: 0.8rem;
  color: rgba(22, 22, 22, 0.5);
}
.browser-item__img {
  height: 14.4rem;
  background-color: #fff;
}
.browser-item__img img {
  height: 100%;
  width: 100%;
}
.browser-item__capture {
  padding: 2.1rem 1rem;
  font-size: 1.2rem;
  line-height: 110%;
  color: rgba(29, 27, 26, 0.8);
}
.browser-item__capture b {
  font-weight: 500;
}

.url {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.3rem 1.1rem;
  padding-right: 1.5rem;
  background-color: #fff;
}
.url__control {
  display: flex;
  align-items: center;
  gap: 1.4rem;
  flex-shrink: 0;
}
.url__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.1rem;
  aspect-ratio: 1/1;
}
.url__btn img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.url__btn--next {
  transform: rotateY(180deg);
  opacity: 0.6;
}
.url__more {
  flex-shrink: 0;
  width: 0.3rem;
  height: 1.1rem;
}
.url__more img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.url__path {
  flex-grow: 1;
  background-color: #f1f3f4;
  border-radius: 1rem;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  color: #767676;
  display: flex;
  align-items: center;
  gap: 0.9rem;
}
.url__path::before {
  content: "";
  display: block;
  width: 0.8rem;
  height: 0.9rem;
  background-image: url("./../img/lock.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}